<template>
    <div class="container">
        <h4 class="text-center q-mb-sm">BQB Token Liquidity Generation</h4>
        <p v-if="!isMetaMaskConnected" class="text-center q-mt-sm event-message">
            Please connect wallet to check the status.
        </p>
        <p v-if="isMetaMaskConnected && resultPeriodEndDate != '0'" class="text-center q-mt-sm event-message">
            Event ends in {{resultPeriodEndDate}}
        </p>
        <p v-if="isMetaMaskConnected && resultPeriodEndDate == '0'" class="text-center q-mt-sm event-message">
            The event has ended.
        </p>
        <div class="q-mt-lg flex justify-center items-baseline">
            <button class="button" @click="detail">Details</button>
            <button class="button" @click="contract">Contract</button>
            <button class="button" @click="claim">Claim</button>
            <button class="button" @click="claimBonus">Bonus Claim</button>
        </div>
        <p v-if="isMetaMaskConnected" class="text-center q-mt-sm">Claiming Date: {{resultBeginDate}} ~ {{resultEndDate}}</p>
        <div class="text-center flex justify-center">
            <p style="max-width: 500px">BloqBall is going to be the most fairly launched token in the history of DeFi.
                Launch of BQB token is a 100% community oriented Liquidity Generation Event that's done through
                one of the best DeFi team on Fantom, SpiritSwap.
            </p>
        </div>
        <div class="justify-center flex items-center">
            <div class="q-pa-lg">
                <div class="justify-center flex items-center q-my-md">
                    <div>
                        <img :src="require(`@/assets/logo/ftm.png`)" style="width: 30px;height:30px"/>
                    </div>
                    <div class="justify-center flex items-baseline q-pl-sm">
                        <h4 class="q-my-none">
                        <div v-if="totalFTM.isGreaterThan(2000)">{{totalFTM.shiftedBy(-3).toFormat(2)}}k</div>
                        <div v-else>{{totalFTM.toFormat(2)}}</div>
                        </h4>
                        <div class="q-pl-sm">FTM</div>
                    </div>
                </div>
                <p class="text-center">Total Deposits</p>
            </div>
            <div class="q-pa-lg">
                <div class="justify-center flex items-center q-my-md">
                    <div>
                        <img :src="require(`@/assets/logo/bqb.png`)" style="width: 30px;height:30px"/>
                    </div>
                    <div class="justify-center flex items-baseline q-pl-sm">
                        <h4 class="q-my-none">{{parseFloat(bqbPrice).toFixed(7)}}</h4>
                        <div class="q-pl-sm">FTM</div>
                    </div>
                </div>
                <p class="text-center">Current Price</p>
            </div>
        </div>
        <div class="flex justify-center wrap">
            <table>
                <tr v-if="isMetaMaskConnected">
                    <td>
                        <div class="label_left">
                            Your FTM Sent : 
                            <div v-if="userSharedFTM.isGreaterThan(2000)">{{userSharedFTM.shiftedBy(-3).toFormat(2)}}k FTM</div>
                            <div v-else>{{userSharedFTM.toFormat(2)}} FTM</div>
                        </div>
                    </td>
                    <td>
                        <div class="label_right">
                            <div>
                                <span>Your BQB (</span>
                                <span>Claimed / </span>
                                <span class="hilight">Claimable</span>
                                <span> / </span>
                                <span class="blue_hilight">Total, </span>
                                <span>Bonus Claimed / </span>
                                <span class="hilight">Claimable</span>
                                <span> / </span>
                                <span class="blue_hilight">Total</span>
                                <span> ) </span>               
                            </div>
                            <div>
                                <span v-if="userClaimedBQB.isGreaterThan(2000)">{{userClaimedBQB.shiftedBy(-3).toFormat(2)}}k</span>
                                <span v-else>{{userClaimedBQB.toFormat(3)}}</span>
                                <span class="hilight" v-if="userSharedBQB.isGreaterThan(2000)"> / {{userSharedBQB.shiftedBy(-3).toFormat(2)}}k</span>
                                <span class="hilight" v-else> / {{userSharedBQB.toFormat(2)}}</span>
                                <span class="blue_hilight" v-if="userTotalSharedBQB.isGreaterThan(2000)"> / {{userTotalSharedBQB.shiftedBy(-3).toFormat(2)}}k BQB</span>
                                <span class="blue_hilight" v-else> / {{userTotalSharedBQB.toFormat(2)}} BQB</span>
                                <span>, </span>
                                <span v-if="userBonusClaimedBQB.isGreaterThan(2000)">{{userBonusClaimedBQB.shiftedBy(-3).toFormat(2)}}k</span>
                                <span v-else>{{userBonusClaimedBQB.toFormat(2)}}</span>
                                <span class="hilight" v-if="userBonusSharedBQB.isGreaterThan(2000)"> / {{userBonusSharedBQB.shiftedBy(-3).toFormat(2)}}k</span>
                                <span class="hilight" v-else> / {{userBonusSharedBQB.toFormat(2)}}</span>
                                <span class="blue_hilight" v-if="userTotalBonusSharedBQB.isGreaterThan(2000)"> / {{userTotalBonusSharedBQB.shiftedBy(-3).toFormat(2)}}k BQB</span>
                                <span class="blue_hilight" v-else> / {{userTotalBonusSharedBQB.toFormat(2)}} BQB</span>
                            </div>
                        </div>
                    </td>
                </tr>
                <tr v-if="resultPeriodEndDate != '0'">
                    <td>
                        <div>
                            <q-input type="text" prefix="FTM" v-model="amountFTM"
                                standout="bg-white text-white" placeholder="0.0"
                                :error-message="errorMessage" :error="errorMessage!=null" :bottom-slots="false" :hide-bottom-space="true">
                                <template v-slot:append>
                                    <div>
                                        <q-btn rounded outline color="blue" size="sm" @click="getMaxFTM">
                                            MAX
                                        </q-btn>
                                    </div>
                                </template>
                            </q-input>
                        </div>
                    </td>
                    <td>
                        <div class="text-center">
                            <div>
                                <button class="action" v-if="!isMetaMaskConnected" @click="connectWallet">Connect to wallet</button>
                                <button class="action" v-if="isMetaMaskConnected" @click="depositFTM"> Add FTM for BQB </button>
                            </div>
                        </div>
                    </td>
                </tr>
            </table>
        </div>
    </div>
</template>

<script>
import BigNumber from 'bignumber.js'
    export default {
        components: {},
        data() {
            return {
                banner: true,
                tab: 'stake',
                img3: '/amg/ftm.png',
                img4: '/amg/bqb.png',
                amountFTM: null
            }
        },
        computed: {
            maxFTM() {
                if(this.$store.state.account) {
                    return BigNumber(this.$store.state.balance.amountFTM)
                        .shiftedBy(-18)
                }
                return 0
            },
            isMetaMaskConnected() {
                return this.$store.state.account!=null;
            },
            resultBeginDate() {
                let date = this.$store.state.liquiditygenerator.vestingBegin;
                const options = { year: 'numeric', month: 'numeric', day: 'numeric', hour: 'numeric', minute: 'numeric' }
                const dtf = new Intl.DateTimeFormat('en-US', options);
                return dtf.format(date)
            },
            resultEndDate() {
                let date = this.$store.state.liquiditygenerator.vestingEnd;
                const options = { year: 'numeric', month: 'numeric', day: 'numeric', hour: 'numeric', minute: 'numeric' }
                const dtf = new Intl.DateTimeFormat('en-US', options);
                return dtf.format(date)
            },
            resultPeriodEndDate() {
                let enddate = this.$store.state.liquiditygenerator.periodEnd;

                let currentTime = Date.now();
                var difference=enddate - currentTime;

                if (difference < 0)
                    return "0"; 

                var daysDifference = Math.floor(difference/1000/60/60/24);
                difference -= daysDifference*1000*60*60*24;

                var hoursDifference = Math.floor(difference/1000/60/60);
                difference -= hoursDifference*1000*60*60;

                var minutesDifference = Math.floor(difference/1000/60);
                difference -= minutesDifference*1000*60;

                var result = daysDifference.toString() + " days " +  hoursDifference.toString() + " hours " + minutesDifference.toString() + " minutes"

                return result
            },
            errorMessage() {
                if(BigNumber(this.amountFTM).isGreaterThan(this.maxFTM)) {
                    return "INSUFFICIENT ACCOUNT BALANCE"
                }
                else if (BigNumber(this.amountFTM) < 0.1){
                    return "MINIMAL AMOUNT VALUE IS 0.1 FTM"
                }
                else if (BigNumber(this.amountFTM) > 10000){
                    return "MAXIMAL AMOUNT VALUE IS 10K FTM"
                }
                return null
            },
            totalFTM() {
                if(this.$store.state.liquiditygenerator.totalFTMShares > 0)
                    return BigNumber(this.$store.state.liquiditygenerator.totalFTMShares).shiftedBy(-18)
                return BigNumber(0)
            },
            bqbPrice() {
                if(this.$store.state.liquiditygenerator.bqbPriceInLGE > 0)
                    return BigNumber(this.$store.state.liquiditygenerator.bqbPriceInLGE)
                return BigNumber(0)
            },
            userSharedFTM() {
                if(this.$store.state.liquiditygenerator.RecipientInfo != null)
                    return BigNumber(this.$store.state.liquiditygenerator.RecipientInfo.shares).shiftedBy(-18)
                return BigNumber(0)
            },
            userTotalSharedBQB() {
                let total;
                let userShare;
                if(this.$store.state.liquiditygenerator.totalFTMShares > 0)
                    total = BigNumber(this.$store.state.liquiditygenerator.totalFTMShares).shiftedBy(-18);
                else
                    return BigNumber(0);

                if(this.$store.state.liquiditygenerator.RecipientInfo != null)
                    userShare = BigNumber(this.$store.state.liquiditygenerator.RecipientInfo.shares).shiftedBy(-18);
                else
                    return BigNumber(0);

                var share = userShare * 100 / total;
                var balance = this.$store.state.liquiditygenerator.normalVestingBQB * share / 100;

                return BigNumber(balance).shiftedBy(-18);
            },
            userSharedBQB() {
                let total;
                let userShare;
                if(this.$store.state.liquiditygenerator.totalFTMShares > 0)
                    total = BigNumber(this.$store.state.liquiditygenerator.totalFTMShares).shiftedBy(-18);
                else
                    return BigNumber(0);

                if(this.$store.state.liquiditygenerator.RecipientInfo != null)
                    userShare = BigNumber(this.$store.state.liquiditygenerator.RecipientInfo.shares).shiftedBy(-18);
                else
                    return BigNumber(0);

                var share = userShare * 100 / total;
                var balance = this.$store.state.liquiditygenerator.normalVestingBQB * share / 100;
                balance = balance - this.$store.state.liquiditygenerator.RecipientInfo.credit;

                return BigNumber(balance).shiftedBy(-18);
            },
            userClaimedBQB() {
                return BigNumber(this.$store.state.liquiditygenerator.RecipientInfo.credit).shiftedBy(-18);
            },
            userTotalBonusSharedBQB() {
                let total;
                let userShare;
                if(this.$store.state.liquiditygenerator.totalFTMShares > 0)
                    total = BigNumber(this.$store.state.liquiditygenerator.totalBonusFTMShares).shiftedBy(-18);
                else
                    return BigNumber(0);

                if(this.$store.state.liquiditygenerator.BonusRecipientInfo != null)
                    userShare = BigNumber(this.$store.state.liquiditygenerator.BonusRecipientInfo.shares).shiftedBy(-18);
                else
                    return BigNumber(0);

                var share = userShare * 100 / total;
                var balance = this.$store.state.liquiditygenerator.bonusVestingBQB * share / 100;

                return BigNumber(balance).shiftedBy(-18);
            },
            userBonusSharedBQB() {
                let total;
                let userShare;
                if(this.$store.state.liquiditygenerator.totalFTMShares > 0)
                    total = BigNumber(this.$store.state.liquiditygenerator.totalBonusFTMShares).shiftedBy(-18);
                else
                    return BigNumber(0);

                if(this.$store.state.liquiditygenerator.BonusRecipientInfo != null)
                    userShare = BigNumber(this.$store.state.liquiditygenerator.BonusRecipientInfo.shares).shiftedBy(-18);
                else
                    return BigNumber(0);

                var share = userShare * 100 / total;
                var balance = this.$store.state.liquiditygenerator.bonusVestingBQB * share / 100;

                balance = balance - this.$store.state.liquiditygenerator.BonusRecipientInfo.credit;

                return BigNumber(balance).shiftedBy(-18);
            },
            userBonusClaimedBQB() {
                return BigNumber(this.$store.state.liquiditygenerator.BonusRecipientInfo.credit).shiftedBy(-18);
            },
        },
        mounted() {
            this.$store.commit('read_liquiditygenerator')
        },
        methods: {
            connectWallet() {                
                this.$store.dispatch('connect')              
            },
            getMaxFTM() {
                this.amountFTM = this.maxFTM
            },
            depositFTM() {
                this.$store.dispatch('depositFTM',{
                    amount:this.amountFTM
                })
            },    
            detail() {
                window.open('http://docs.bloqball.com/bqb/bqb-fair-launch-with-spiritswap-liquidity-generation-event', '_blank')
            },   
            contract() {
                this.$store.dispatch('contractLink')
            },   
            claim() {
                this.$store.dispatch('claimBQB')
            },   
            claimBonus() {
                this.$store.dispatch('claimBQBForBonus')
            },  
        }
    }
</script>

